.counter {
  background: #ffbf3b;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(30%, -30%);
  border-radius: 50%;
  font-size: 0.7rem;
  width: 1rem;
  height: 1rem;
  text-align: center;
  color: #20c997;
  animation: pop-in 0.3s 1 cubic-bezier(0, 0, 0, 2.23); }

.header {
  padding-left: .5rem;
  padding-right: .5rem;
  padding-top: .3rem;
  padding-bottom: .3rem;
  font-size: .8rem;
  color: black;
  border-bottom: #666666 solid 1px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #666666; }

.stateToggler {
  border-right: 1px solid grey;
  padding-right: 0.5rem;
  margin-right: .5rem;
  line-height: 1;
  cursor: pointer; }
  .stateToggler:hover {
    text-decoration: underline; }

.notifsList {
  max-height: 60vh;
  overflow-y: auto; }

.noMentions {
  padding: 1rem .3rem; }

.singleRow {
  display: flex;
  align-items: center;
  width: 100%;
  padding: .3rem 0;
  cursor: pointer; }
  .singleRow:not(:last-child) {
    border-bottom: #BBBBBB solid 1px; }

.thumbnail {
  max-width: 4rem;
  flex: 0 0 4rem;
  height: auto;
  margin-left: .5rem;
  margin-right: .5rem; }

.text {
  flex-grow: 2; }

.notifier {
  font-style: italic;
  color: #666666; }

.status:before {
  content: '';
  display: inline-block;
  width: 5px;
  height: 5px;
  background-color: #20c997;
  border-radius: 50%;
  margin-right: 1rem; }

@keyframes wiggle {
  0%, 20%, 80%, 100% {
    transform: rotate(0deg) scale(1); }
  25% {
    transform: rotate(-10deg) scale(1.1); }
  75% {
    transform: rotate(10deg) scale(1.1); } }



