.links_container {
  background: black;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  filter: brightness(50%);
  display: flex;
  flex-flow: row wrap;
  overflow: auto; }
  .links_container:after {
    content: '';
    flex-grow: 50; }

body .thumb {
  display: flex;
  align-items: center;
  justify-content: center;
  border: white solid 1px;
  pointer-events: none;
  min-height: 80px;
  flex: 1 1 20%; }
  @media (min-width: 767px) {
    body .thumb {
      flex: 1 1 10%; } }

.provider {
  color: white;
  position: absolute;
  right: .5rem;
  bottom: .5rem;
  font-size: .7rem; }
  @media (min-width: 991px) {
    .provider {
      font-size: .8rem; } }



