.infos {
  padding: 10px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #111;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.6); }
  .infos:hover {
    background-color: rgba(255, 255, 255, 0.3); }

.bottom {
  flex-grow: 10;
  display: inline-flex;
  align-items: flex-end;
  justify-content: space-between; }

.tags {
  margin-top: .5rem;
  opacity: .7; }
  .tags i {
    opacity: .5;
    margin-right: 3px; }

.title {
  margin-top: 0;
  font-size: 1.2rem; }

.submitted {
  font-size: .9rem; }

.controls {
  float: right;
  display: inline-block; }

.user_icon {
  color: #444444;
  margin-right: .5rem; }

.full_bg {
  width: 100%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 5px; }
  @media (max-width: 767px) {
    .full_bg {
      min-height: 20vh; }
      .full_bg img {
        display: none; } }

.fade {
  filter: grayscale(100%);
  transition: filter .3s ease-in-out; }
  .fade:hover {
    transition: none;
    filter: none; }

.playingLogo {
  font-size: 3rem;
  color: #20c997;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }



