.pageTitle {
  font-size: 3rem;
  margin-top: 65px;
  padding-left: 10px;
  position: sticky;
  top: 0;
  z-index: -1; }
  @media (min-width: 767px) {
    .pageTitle {
      font-size: 8rem;
      margin-top: 0px; } }

@media (min-width: 767px) {
  .borderTop {
    border-top: solid 2px #20c997;
    position: sticky;
    top: 0;
    width: 100%;
    z-index: 1; } }

.container__grid {
  background: white;
  display: flex;
  flex-flow: row wrap;
  width: 100%;
  padding: 7.5px; }
  .container__grid:after {
    content: '';
    flex-grow: 50; }

.item__grid {
  display: inline-flex;
  flex: 1 1 100%;
  animation: fade-in-up .3s 1 ease-out forwards;
  opacity: 0;
  padding: 7.5px;
  position: relative;
  margin-bottom: 3rem; }
  @media (min-width: 767px) {
    .item__grid {
      flex: 1 1 calc(33.333% - 10px); } }
  @media (min-width: 991px) {
    .item__grid {
      flex: 1 1 calc(33.33% - 10px); } }
  @media (min-width: 1200px) {
    .item__grid {
      flex: 1 1 calc(16.666% - 10px); } }
  .item__grid:hover, .item__grid.playing {
    cursor: pointer; }
  .item__grid iframe {
    width: 100%;
    height: 100%; }
  .item__grid .nouse-icon {
    opacity: .3; }
  .item__grid .thumbnail {
    margin-bottom: 0; }
  .item__grid.no__spacing {
    padding: 0px;
    margin: 0; }

.loading_item {
  display: flex;
  justify-content: center;
  align-items: center;
  border: solid 1px #BBBBBB;
  color: #BBBBBB;
  padding: 10px;
  border-radius: 5px;
  animation: fade-in-up .3s .8s 1 ease-out forwards; }

@keyframes fade-in-up {
  from {
    opacity: 0;
    transform: translateY(10px); }
  to {
    opacity: 1;
    transform: translateY(0px); } }



