.container {
  display: flex;
  flex-flow: column;
  align-items: left; }
  .container > div {
    padding: 0 10px; }

.controls {
  display: flex;
  flex-flow: row;
  align-items: center;
  margin-right: .5rem; }

.player_toggler {
  margin-right: .5rem;
  margin-left: .6rem;
  padding-top: 3px; }

.progress {
  max-width: 100%;
  min-width: 100%; }

.track_info {
  display: flex;
  flex-flow: row;
  margin-bottom: .5rem;
  cursor: pointer; }

.track_name {
  max-width: 10ch;
  cursor: pointer; }

.vertical.container > div {
  padding: 0; }

.vertical .progress {
  min-width: 75%;
  max-width: 75%; }

.vertical .track_name {
  max-width: 15ch;
  font-size: smaller; }



