.container {
  position: relative;
  display: flex;
  height: calc(100vh - 6rem);
  overflow: auto;
  flex-direction: column;
  color: #20c997;
  justify-content: space-between;
  align-items: center;
  border: 2rem solid rgba(202, 205, 220, 0.95);
  border-top: none;
  border-bottom: none;
  background: rgba(27, 30, 42, 0.8); }

.container_header {
  background-color: rgba(202, 205, 220, 0.95);
  height: 3rem;
  color: black;
  text-align: center;
  vertical-align: middle;
  text-transform: uppercase;
  padding-left: 2rem;
  padding-right: 2rem;
  line-height: 3rem; }
  .container_header:last-child {
    line-height: 1.42; }

.center {
  flex-grow: 50;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column; }

.slogan {
  position: absolute;
  padding: 5px 10px;
  right: 0;
  transform-origin: top right;
  transform: rotate(90deg) translateX(100%);
  font-size: 0.875rem;
  color: white; }



