.site_name {
  color: #20c997;
  text-align: center;
  font-size: 2rem;
  font-weight: normal;
  animation: pop-in 0.5s 1 cubic-bezier(0.33, 1.06, 0.27, 1.34);
  margin: auto;
  max-width: 160px; }
  .site_name .top {
    font-size: 1.8em;
    font-weight: bolder;
    line-height: .5;
    padding-top: .5rem; }
  @media (min-width: 767px) {
    .site_name {
      font-size: 2rem; }
      .site_name .top {
        font-size: 1.8em; } }
  .site_name .bottom {
    opacity: .5; }
  .site_name:hover {
    transition: all 5s ease-out; }
    .site_name:hover span {
      display: inline-block; }

.large {
  max-width: 500px; }

@keyframes pop-in {
  from {
    transform: scale(0.2); }
  to {
    transform: scale(1); } }

@keyframes rotate {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }



