.container {
  background-color: #26dca6;
  margin: auto;
  color: white;
  height: 100vh;
  display: flex;
  flex-direction: column; }
  .container label {
    padding-left: .5rem;
    font-family: Montserrat, sans-serif; }

.form_container {
  flex-grow: 1;
  overflow: auto;
  padding-top: 1rem;
  transform: translateY(-100%); }
  .form_container.loaded {
    transition: transform .15s ease-in-out;
    transform: translateY(0); }

.form_container, .header {
  padding: 5px; }

.header {
  display: inline-flex;
  flex-shrink: 0;
  align-items: center;
  background-color: #20c997;
  color: white;
  padding-right: 40px;
  border-bottom: 1px solid;
  z-index: 1;
  min-height: 75px; }

.title {
  margin-top: 0;
  margin-bottom: 0;
  font-size: larger; }

.url {
  color: #c0f5e5;
  font-size: smaller;
  font-style: italic; }

.thumbnail {
  max-height: 3rem;
  margin-right: 1rem; }

.help_text {
  font-style: italic;
  margin-left: .5rem;
  color: #c0f5e5; }

.state_info {
  color: #c0f5e5;
  font-style: italic;
  display: inline-block;
  margin-left: .5rem; }



